}
:root {
    --globalWidth: 70vw;
}
body {
  margin: 0;
  -webkit-tap-highlight-color: transparent;
}
.all {
    display: grid;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -ms-flex-line-pack: start;
        align-content: start;
    grid-template-columns: var(--globalWidth);
    grid-template-rows: auto;
    overflow: hidden;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
