    position: relative;
}
a {
    color: white;
    position:relative;
    text-decoration: none;
    text-transform: none;
}
a:hover {
    font-weight: bold;
}
a:active {
    color: #fff152;
}
.footer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr auto;
    grid-gap: 2px;
    margin: 20px 0 20px 0;
    justify-items: center;
