}
.footerSection > span:hover {
    cursor: pointer;
    font-weight: bold;
    line-height: 1.5;
}
.footerSection > span:active{
    color: #fff152;
}
.myAvatar {
    cursor: pointer;
    position: relative;
    width: 90%;
    max-width: 116px;
    margin: 10px;
    border-radius: 50%;
    -webkit-transition: -webkit-transform 0.25s ease-in-out, -webkit-box-shadow 0.25s ease-in-out;
    transition: -webkit-transform 0.25s ease-in-out, -webkit-box-shadow 0.25s ease-in-out;
    transition: transform 0.25s ease-in-out, box-shadow 0.25s ease-in-out;
    transition: transform 0.25s ease-in-out, box-shadow 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out, -webkit-box-shadow 0.25s ease-in-out;
}
.myAvatar:hover {
    -webkit-box-shadow: 2px 2px 0px #ffffff, -2px -2px 0px #ffffff, -2px 2px 0px #ffffff, 2px -2px 0px #ffffff;
            box-shadow: 2px 2px 0px #ffffff, -2px -2px 0px #ffffff, -2px 2px 0px #ffffff, 2px -2px 0px #ffffff;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}
.myAvatar:active {
    -webkit-box-shadow: 2px 2px 0px #fff152, -2px -2px 0px #fff152, -2px 2px 0px #fff152, 2px -2px 0px #fff152;
