}
.myAvatar:hover + .omnie {
    opacity: 1;
}
.omnie {
    pointer-events: none;
    position: absolute;
    font-weight: bold;
    margin-top: -7px;
    opacity: 0;
    text-shadow: 1px 1px 2px black;
    -webkit-transition: opacity 0.25s ease-in-out;
    transition: opacity 0.25s ease-in-out;
}
.footerSection > iframe {
    width: 90%;
    max-width: 200px;
}
.beerNav {
    width: var(--globalWidth);
    margin: 20px 0 20px 0;
    display: grid;
    grid-template-columns: repeat(3, minmax(50px, 1fr)) 200px repeat(3, minmax(50px, 1fr));
    grid-template-rows: 90px 70px;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -ms-flex-line-pack: start;
        align-content: start;
    border-bottom: solid #FA9C1E 2px;
    justify-items: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.headerLogo {
    display: none;
    text-align: center;
    margin: 50px 0 20px 25px;
    cursor: pointer;
    font-family: "SteakSmoked";
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
}
.beerNav > .headerLogo {
    display: block;
    margin: 50px 0 0 25px;
}
.headerLogo span:nth-child(1) {
    font-size: 85px;
    line-height: 0px;
}
.headerLogo span:nth-child(3) {
    font-size: 30px;
    line-height: 50px;
}
.lewarek {
    position: relative;
    cursor: pointer;
}
.rod {
    width: 10px;
    height: 24px;
    margin: auto;
    border-bottom: solid #1D1D1B 2px; 
    position: relative;
    -webkit-transition: background 0.2s linear, -webkit-transform 0.2s linear;
    transition: background 0.2s linear, -webkit-transform 0.2s linear;
    transition: transform 0.2s linear, background 0.2s linear;
    transition: transform 0.2s linear, background 0.2s linear, -webkit-transform 0.2s linear;
}
.rod:before {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background:  -webkit-gradient(linear, left top, right top, from(#FA9C1C), color-stop(50%, #FA9C1C), color-stop(50%, #FBB049), to(#FBB049));
    background:  linear-gradient(90deg, #FA9C1C 0%, #FA9C1C 50%, #FBB049 50%, #FBB049 100%);
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    border: solid #1D1D1B 3px;
    border-bottom: transparent;
    -webkit-transform: scaleX(0.9);
            transform: scaleX(0.9);
}
.rod:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #1D1D1B;
    -webkit-transform: scaleX(0.9);
            transform: scaleX(0.9);
    top: calc(100% + 1px + 2px);
}
.wheel {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
    width: 16px;
    height: 16px;
    margin: auto;
    margin-top: 4px;
    background: radial-gradient(ellipse at center, #ffffff 0%,#ededed 50%,#1d1d1b 52%,#1d1d1b 64%,#7b7b53 66%,#7b7b53 100%);
    border-radius: 37.5px;
    border: solid #1D1D1B 2px;
}
.wheel:before {
    content: '';
    position: absolute;
    width: 100%;
	height: 100%;
    background-color: #1D1D1B;
    border-radius: 50px;
    -webkit-transform: rotate(45deg) scale(0.1,0.45);
            transform: rotate(45deg) scale(0.1,0.45);
}
.wheel:after {
    content: '';
    position: absolute;
    width: 100%;
	height: 100%;
    background-color: #1D1D1B;
    border-radius: 50px;
    -webkit-transform: rotate(-45deg) scale(0.1,0.45);
            transform: rotate(-45deg) scale(0.1,0.45);
}
.tap {
    width: 4px;
    height: 10px;
    margin: auto;
    position: relative;
}
.tap:before {
    content: '';
    position: absolute;
    width: 100%;
	height: 100%;
    background-color: #1D1D1B;
    -webkit-transform: perspective(9px) rotatex(-45deg);
            transform: perspective(9px) rotatex(-45deg);
}
.tap:after {
    content: '';
    position: absolute;
    width: calc(50% - 1px);
	height: 104px;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-transform-origin: top;
            transform-origin: top;
    border-right: solid #FBB049 2px;
    z-index: -1;
}
.textMenu {
    font-weight: bold;
    font-size: 15px;
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
    -webkit-transition: -webkit-transform 0.15s linear;
    transition: -webkit-transform 0.15s linear;
    transition: transform 0.15s linear;
    transition: transform 0.15s linear, -webkit-transform 0.15s linear;
}
.glass {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 36px;
    height: 70px;
    border: solid #1D1D1B 6px; 
    position: relative;
    -webkit-transform: translateY(7px) translateX(calc((var(--globalWidth) - 200px) / 12*-5 - 100px)) perspective(60px) rotatex(-15deg);
            transform: translateY(7px) translateX(calc((var(--globalWidth) - 200px) / 12*-5 - 100px)) perspective(60px) rotatex(-15deg);
    overflow: hidden;
    z-index: -1;
    grid-column: 1 / 8;
    -webkit-transition: -webkit-transform 0.25s linear;
    transition: -webkit-transform 0.25s linear;
    transition: transform 0.25s linear;
    transition: transform 0.25s linear, -webkit-transform 0.25s linear;
}
@supports (-ms-ime-align: auto) { /*Tylko dla MS Edge, bo nie obsługuje kombinacji calc(var()) od 5 lat*/
.glass {
       -webkit-transition: none;
       transition: none;
}
}
.glass:before {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    content: '';
    position: absolute;
	width: 100%;
	height: 100%;
    background:  -webkit-gradient(linear, left top, right top, from(#FA9C1C), color-stop(50%, #FA9C1C), color-stop(50%, #FBB049), to(#FBB049));
    background:  linear-gradient(90deg, #FA9C1C 0%, #FA9C1C 50%, #FBB049 50%, #FBB049 100%);
    -webkit-transform: translateY(59px);
            transform: translateY(59px);
}
.lewarek:hover > .rod, .lewarek:hover > .tap, .lewarek:hover > .wheel {
    -webkit-box-shadow: 3px 1px 2px #fff152;
            box-shadow: 3px 1px 2px #fff152;
}
.lewarek:hover > .textMenu {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    text-shadow: 3px 1px 1px #fff152;
}
.lewarek:active > .textMenu {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    text-shadow: 0px 0px 3px #1D1D1B;
    color: #fff152;
}
.lewarek:hover > .rod {
    -webkit-transform: translateY(8px) perspective(15px) rotateX(-15deg);
            transform: translateY(8px) perspective(15px) rotateX(-15deg);
}
.lewarek:hover > .rod:before {
    background:  linear-gradient(80deg, #FA9C1C 0%, #FA9C1C 50%, #FBB049 50%, #FFD59C 100%);
}
.lewarek:hover > .wheel:before{
    -webkit-animation: wheelRotation 0.5s linear 0.125s 6;
            animation: wheelRotation 0.5s linear 0.125s 6;
}
.lewarek:hover > .wheel:after{
    -webkit-animation: wheelRotation 0.5s linear 6;
            animation: wheelRotation 0.5s linear 6;
}
.lewarek:hover > .tap:after {
    -webkit-animation: tapLiquid 3.25s linear forwards;
            animation: tapLiquid 3.25s linear forwards;
}
.beerNav > .lewarek:hover ~ .glass:before {
    -webkit-animation: glassLiquid 3s linear forwards 0.25s;
            animation: glassLiquid 3s linear forwards 0.25s;
}
.beerNav > .lewarek:first-child:hover ~ .glass {
    -webkit-transform: translateY(7px) translateX(calc((var(--globalWidth) - 200px) / 12*-5 - 100px)) perspective(60px) rotatex(-15deg);
            transform: translateY(7px) translateX(calc((var(--globalWidth) - 200px) / 12*-5 - 100px)) perspective(60px) rotatex(-15deg);
}
.beerNav > .lewarek:nth-child(2):hover ~ .glass {
    -webkit-transform: translateY(7px) translateX(calc((var(--globalWidth) - 200px) / 12*-3 - 100px)) perspective(60px) rotatex(-15deg);
            transform: translateY(7px) translateX(calc((var(--globalWidth) - 200px) / 12*-3 - 100px)) perspective(60px) rotatex(-15deg);
}
.beerNav > .lewarek:nth-child(3):hover ~ .glass {
    -webkit-transform: translateY(7px) translateX(calc((var(--globalWidth) - 200px) / 12*-1 - 100px)) perspective(60px) rotatex(-15deg);
            transform: translateY(7px) translateX(calc((var(--globalWidth) - 200px) / 12*-1 - 100px)) perspective(60px) rotatex(-15deg);
}
.beerNav > .lewarek:nth-child(5):hover ~ .glass {
    -webkit-transform: translateY(7px) translateX(calc((var(--globalWidth) - 200px) / 12*1 + 100px)) perspective(60px) rotatex(-15deg);
            transform: translateY(7px) translateX(calc((var(--globalWidth) - 200px) / 12*1 + 100px)) perspective(60px) rotatex(-15deg);
}
.beerNav > .lewarek:nth-child(6):hover ~ .glass {
    -webkit-transform: translateY(7px) translateX(calc((var(--globalWidth) - 200px) / 12*3 + 100px)) perspective(60px) rotatex(-15deg);
            transform: translateY(7px) translateX(calc((var(--globalWidth) - 200px) / 12*3 + 100px)) perspective(60px) rotatex(-15deg);
}
.beerNav > .lewarek:nth-child(7):hover ~ .glass {
    -webkit-transform: translateY(7px) translateX(calc((var(--globalWidth) - 200px) / 12*5 + 100px)) perspective(60px) rotatex(-15deg);
            transform: translateY(7px) translateX(calc((var(--globalWidth) - 200px) / 12*5 + 100px)) perspective(60px) rotatex(-15deg);
}
.router-link-active:hover > .rod {
    -webkit-transform: none;
            transform: none;
}
.router-link-active:hover > .wheel:after{
    -webkit-animation: paused;
            animation: paused;
}
.router-link-active:hover > .wheel:before{
    -webkit-animation: paused;
            animation: paused;
}
.router-link-active:hover > .tap:after {
    -webkit-animation: paused;
            animation: paused;
}
.router-link-active:hover > .textMenu {
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
    text-shadow: 3px 1px 1px #fff152;
}
.beerNav > .router-link-active ~ .glass:before {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
}
.beerNav > .router-link-active:hover ~ .glass:before {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
    -webkit-animation: paused;
            animation: paused;
}
.slide-enter-active, .slide-leave-active {
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transition: -webkit-transform 0.5s ease-in-out;
    transition: -webkit-transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}
.slide-enter {
    -webkit-transform: translateX(-100vw);
            transform: translateX(-100vw);
}
.slide-leave-to {
    -webkit-transform: translateX(100vw);
            transform: translateX(100vw);
}
.burgerFalseButton {
    opacity: 0;
}
.burgerFalseButton, .burgerButton {
    display: none;
    position: fixed;
    width: 40px;
    height: 40px;
    margin: 10px;
    z-index: 2;
}
.burgerFalseButton:checked ~ .beerNav {
    -webkit-transform: none;
            transform: none;
}
.burgerButton {
    pointer-events: none;
}
.burgerButton:after, 
.burgerButton:before, 
.burgerButton div {
    background-color: #474745;
    -webkit-box-shadow: 0 0 8px white;
            box-shadow: 0 0 8px white;
    border-radius: 3px;
    content: '';
    display: block;
    height: 5px;
    margin: 7px 0;
    -webkit-transition: -webkit-transform .2s ease-in-out;
    transition: -webkit-transform .2s ease-in-out;
    transition: transform .2s ease-in-out;
    transition: transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
}
.burgerFalseButton:checked ~ .burgerButton:before {
    -webkit-box-shadow: none;
            box-shadow: none;
    -webkit-transform: translateY(12px) rotate(135deg);
            transform: translateY(12px) rotate(135deg);
}
.burgerFalseButton:checked ~ .burgerButton:after {
    -webkit-box-shadow: none;
            box-shadow: none;
    -webkit-transform: translateY(-12px) rotate(-135deg);
            transform: translateY(-12px) rotate(-135deg);
}
.burgerFalseButton:checked ~ .burgerButton div {
    -webkit-transform: scale(0);
            transform: scale(0);
}
@-webkit-keyframes wheelRotation {
from {-webkit-transform: rotate(0deg) scale(0.1,0.45);transform: rotate(0deg) scale(0.1,0.45);
}
to {-webkit-transform: rotate(359deg) scale(0.1,0.45);transform: rotate(359deg) scale(0.1,0.45);
}
}
@keyframes wheelRotation {
from {-webkit-transform: rotate(0deg) scale(0.1,0.45);transform: rotate(0deg) scale(0.1,0.45);
}
to {-webkit-transform: rotate(359deg) scale(0.1,0.45);transform: rotate(359deg) scale(0.1,0.45);
}
}
@-webkit-keyframes glassLiquid {
from {-webkit-transform: translateY(59px);transform: translateY(59px);
}
to {-webkit-transform: translateY(5px);transform: translateY(5px);
}
}
@keyframes glassLiquid {
from {-webkit-transform: translateY(59px);transform: translateY(59px);
}
to {-webkit-transform: translateY(5px);transform: translateY(5px);
}
}
@-webkit-keyframes tapLiquid {
0% {-webkit-transform: scaleY(0);transform: scaleY(0);
}
7.7% {-webkit-transform: scaleY(1);transform: scaleY(1);
}
92.3% {-webkit-transform: scaleY(1);transform: scaleY(1);
}
100% {-webkit-transform: scaleY(0);transform: scaleY(0); -webkit-transform-origin: bottom; transform-origin: bottom;
}
}
@keyframes tapLiquid {
0% {-webkit-transform: scaleY(0);transform: scaleY(0);
}
7.7% {-webkit-transform: scaleY(1);transform: scaleY(1);
}
92.3% {-webkit-transform: scaleY(1);transform: scaleY(1);
}
100% {-webkit-transform: scaleY(0);transform: scaleY(0); -webkit-transform-origin: bottom; transform-origin: bottom;
}
}
@media (max-width: 830px) {
:root {
        --globalWidth: 96vw;
}
}
@media (max-width: 510px) { /*Zmiana menu*/
.headerLogo {
        display: block;
}
.glass, .footerSection:nth-child(3), .beerNav > .headerLogo {
        display: none;
}
.beerNav {
        grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
        grid-template-rows: auto;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        position: fixed;
        width: 100%;
        height: 50vh;
        overflow-y: scroll;
        background-color: #ffffff;
        opacity: 0.9;
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        margin: 0;
        padding: 60px 10px 60px 10px;
        -webkit-transition: -webkit-transform 0.25s linear;
        transition: -webkit-transform 0.25s linear;
        transition: transform 0.25s linear;
        transition: transform 0.25s linear, -webkit-transform 0.25s linear;
        z-index: 1;
}
.lewarek {
        margin: 10px;
}
.lewarek:hover > .tap:after {
        -webkit-animation: none;
                animation: none;
}
.footer {
        grid-template-columns: repeat(2, 1fr);
}
.burgerFalseButton, .burgerButton {
        display: block;
}
}
